import React ,{useState} from 'react';
import {CheckOutlined } from "@ant-design/icons"
import {Modal} from 'antd'
const Etc = ({ data }) => {

  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = (e) => {
    setIsModalVisible(true);
    console.log(e)
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

return(

  <section >
    <h1 className="section-header">Etc</h1>
    {data.map((item,index) => (
      <>
        <article className="my-5" key={item.name}>
        {index ===0 ? 
          <div><a href={item.link2}><h2 className="item-header">{item.name}</h2></a>
          <h3 className="item-sub">
              {item.company} | {item.period} 
          </h3>  
          <div className="project-contents" >  
          <div className="project-image-contents" onClick = {showModal}>
          <img className="project-image" src={`./${item.img}.jpg`} alt={item.img}></img>
          </div>
          <div className="project-contents-description" >
          <br/>
          <ul>
          <li className="item-ul"><CheckOutlined/> React Dom   </li>
          <li className="item-ul"> <CheckOutlined/> UI  </li>
          <li className="item-ul">  <CheckOutlined/>     </li>
          <li className="item-ul"><CheckOutlined/>Redux React-Router    SPA  </li>
          </ul>
      </div></div>
      <Modal title="" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
      <img  style ={{width : "450px" , height : "470px"}} className="project-image" src={`./${item.img}.jpg`} alt={item.img}></img>
      </Modal>
          </div>
          : <div><a href={item.link}><h2 className="item-header">{item.name}</h2></a>
        <h3 className="item-sub">
            {item.company} | {item.period} 
        </h3> 
        <div className="project-contents" >
        {item.description}
    </div>
        </div> }        
        </article>
      </>
    ))}
  </section>
    )}

export default Etc;

